

const domain = 'http://127.0.0.1:3000';

 export const upload_products = async(data)=>{
	let {seller_id , currency} = JSON.parse(localStorage.getItem('store_'))
		let response = await fetch(`${domain}/add_product` , { method:'POST', headers:{ 'content-type':'application/json' }, body:JSON.stringify({product:{...data, seller_id , currency}}) });
		response = await response.json();
		return response
}

export const get_products = async()=>{
	let {seller_id} = JSON.parse(localStorage.getItem('store_'));
	let seller = await fetch(`${domain}/get_seller/${seller_id}`);
	seller = await seller.json();
	let {products_uploaded} = seller.seller;
	if(products_uploaded.length >0){
	for(let i in products_uploaded){
	if(products_uploaded[i].properties.length > 0){
			products_uploaded[i] = {...products_uploaded[i],...products_uploaded[i].properties[0]}
	}}}
	return products_uploaded.reverse() || []
}

export const delete_products = async(id)=>{
	let response = await fetch(`${domain}/product/${id}` , {method : 'DELETE' ,headers:{'content-type':'application/json'}});
	response = await response.json()
	return response
}